.button-deactivated {
  font-size: 1rem;
  /* padding: 5px 10px; */
  color:   #585858;
	cursor: pointer;
	text-align: center;
	margin: 0;
	padding: 0;
}

.button-activated {
  font-size: 1rem;
  /* padding: 5px 10px; */
  font-weight: bold;
  color: #1565c0;
	cursor: pointer;
	text-align: center;
	margin: 0;
	padding: 0;
}

img.resize-like {
	width: 1.9vw;
  /* max-height: 30%; */
	cursor: pointer;
	margin: 0;
	padding: 0;
	&:active {
		border-radius: 15px;
		border: 3px solid #ffc200;
	}
}

img.resize-dislike {
  max-width: 1.9vw;
  /* max-height: 30%; */
  cursor: pointer;
	transform: rotate(180deg);
	margin: 0;
	padding: 0;
	&:active {
		border-radius: 15px;
		border: 3px solid #ffc200;
	}
}