.sidebar {
  position: fixed;
  top: 0;
  left: -150px;
  width: 150px;
  height: 222px;
  background-color: #555;
  transition: left 300ms ease-out;
}
.sidebar.active {
  left: 0;
}

.sidebar ul {
  margin-top: 10%;
  padding: 0;
}
.sidebar li {
  list-style: none;  
}
.sidebar li a {
  text-decoration: none;
  padding: 10px;
  display: block;
  color: #fff;
  &:hover {
    color: #ffc200;
  }
}

.sandwich {
  border: none;
  outline: 0;
  width: 50px;
  position: absolute;
  right: -50px;
  background-color: #ffc200;
  cursor: pointer;
}

button.sandwich:focus {
  outline: 1px none; 
}

.sandwich:after,
.sandwich:before,
.sandwich div {
  background-color: #fff;
  height: 5px;
  margin: 7px 0;
  border-radius: 3px;
  border: none;
  content: "";
  display: block;
  transition: all 300ms ease-in-out;
}

.sidebar.active .sandwich:before {
  transform: translateY(12px) rotate(135deg);
}
.sidebar.active .sandwich::after {
  transform: translateY(-12px) rotate(-135deg);
}
.sidebar.active .sandwich div {
  transform: scale(0);
}
